import mqtt from '@taoqf/react-native-mqtt';

import * as CONST from './constants';
import Device from "./device_mdl";

const MQTT_CNX = mqtt.connect(CONST.BROKER);
var notify_tmo = null;

var admin_data = {
    connected: [],
    disconnected: [],
};

let callbacks = [];

const _msgListener = MQTT_CNX.on('message', onMessageRx);

MQTT_CNX.on('reconnect', (err, arg2) => {
    console.debug('on reconnect');
});
MQTT_CNX.on('close', (err, arg2) => {
    console.debug('on close');
});
MQTT_CNX.on('disconnect', (err, arg2) => {
    console.debug('on disconnect');
});
MQTT_CNX.on('offline', (err, arg2) => {
    console.debug('on offline');
});
MQTT_CNX.on('error', (err, arg2) => {
    console.debug('on error');
});
MQTT_CNX.on('end', (err, arg2) => {
    console.debug('on end');
});

MQTT_CNX.on('connect', (err, arg2) => {
    console.log("connected to MQTT broker");

    MQTT_CNX.subscribe(CONST.MQTT_MAIN_TOPIC + "/#", err => {
        if (!err) {
          console.debug("Subscribe successful");
        } else {
          console.warn('Failed to subscribe');
          alert('ERROR: Unable to reach MQTT Server: ', err);
        }
      }
    );
});

function onMessageRx(topic, message) {
    // console.log(topic, message.toString());

    // get the device, or create it
    let device = Device.getDeviceForTopic(topic);

    if (device == null) {
        // no valid device found in topic string
        return;
    }

    // set new data
    device.updateFromRx(topic, message.toString());

    clearTimeout(notify_tmo);

    // notify UI via callback
    notify_tmo = setTimeout(notify, 200);
}

function notify() {
    let devices = Device.getDevices();

    console.log("Notifying!");

    // console.log(`Printing devices (${Object.keys(devices).length})`);
    // for (let id in devices) {
    //     const d = devices[id];
    //     console.log(d.toString() + "\n");
    // }

    for (let i = 0; i < callbacks.length; i++) {
        callbacks[i](devices);
    }

}

export function subscribe(cb) {
    callbacks.push(cb);
}


