import React, {useState, useEffect} from 'react';
import { StyleSheet, Text, View, TextInput, ScrollView, SafeAreaView, Dimensions, Pressable } from 'react-native';

import {
    LineChart,
    BarChart,
    PieChart,
    ProgressChart,
    ContributionGraph,
    StackedBarChart,
  } from 'react-native-chart-kit';

import { Ionicons } from '@expo/vector-icons';

import * as DataProv from "./data_provider";
import Device from "./device_mdl";
import * as STYLES from "./commonStyles";
import {TEST_DEVICES} from "./test_data";

const screenWidth = Dimensions.get("window").width;
const screenHeight = Dimensions.get("window").height;


const chartConfig = {
    backgroundGradientFrom: '#FFFFFF',
    backgroundGradientTo: '#FFFFFF',
    decimalPlaces: 0,
    color: (opacity = 1) => `rgba(80, 80, 80, ${opacity})`,
    strokeWidth: 0, // optional, default 3
    barPercentage: 0.8,
    useShadowColorFromDataset: false // optional
};

let EMPTY_BAR_DATA = {
    labels: [],
    datasets: [
        {
            data: [],
            colors: []
        }
    ]
};

const getChartWidth = () => {
    if (screenWidth > 400) {
        return screenWidth / 3;
    } else {
        return screenWidth / 1.5;
    }
};

function CommonBarChart(props) {

    let horizLines = 3;

    if (!props.data || !props.data.length) {
        horizLines = 2;
    }

    return (
        <View style={[styles.chartContainer]}>
            <View style={styles.chartWrap}>
                <Text style={styles.chartHeader}>{props.title}</Text>
                <BarChart
                    key={Math.random() + ""}
                    style={{
                        marginVertical: 8,
                        borderRadius: 16,
                        
                    }}
                    data={props.data}
                    width={getChartWidth()}
                    height={200}
                    chartConfig={chartConfig}
                    yAxisInterval={10}
                    segments={horizLines}
                    yLabelsOffset={props.yLabelsOffset || 35}
                    withCustomBarColorFromData
                    flatColor
                    fromZero
                    withInnerLines
                    showValuesOnTopOfBars
                    showBarTops={false}
                    {...props}
                />
            </View>
        </View>
    );
}

function ConnectedDisconnected() {
    const [connected, setConnected] = useState([]);
    const [disconnected, setDisconnected] = useState([]);

    const handleNewData = (devices) => {
        let connectedDevs = [];
        let disconnDevs = [];
        for (let d in devices) {
            if (devices[d].status != 99) {
                connectedDevs.push(d);
            } else {
                disconnDevs.push(d);
            }
        }

        setConnected(connectedDevs);
        setDisconnected(disconnDevs);
    };

    useEffect(() => {
        // subscribe to our data provider
        DataProv.subscribe(handleNewData);
    }, [])


    let data = {
        labels: ["Connected", "Disconnected"],
        datasets: [
            {
                data: [connected.length, disconnected.length],
                colors: [() => "#00FF00", () => "red"]
            }
        ]
    };

    return (
        <CommonBarChart data={data} title={"Connection Status"}/>
    );
}

function VersionPieChart(props) {
    const [versionCounts, setversionCounts] = useState([]);

    let versionProp = "";
    let chartTitle = "";
    if (props.processor == "ESP") {
        versionProp = "espFwRevision";
        chartTitle = "ESP32 Firmware";
    } else {
        versionProp = "TiFwRevision";
        chartTitle = "MSP430 Firmware";
    }

    const handleNewData = (devices) => {
        let counts = {};

        for (let d in devices) {
            if (props.connectedOnly && devices[d].status == 99) continue;

            let thisVers = devices[d].system[versionProp];

            if (!thisVers) {
                thisVers = "Unknown";
            } else {
                thisVers = "v" + thisVers;
            }

            if (counts[thisVers] == null) {
                counts[thisVers] = 1;
            } else {
                counts[thisVers] += 1;
            }
        }

        let colorMap = ["#886677", "#33DDBB", "yellow", "cyan", "#CC7777", "#CDCDCD", "#00FF00", "gray"]

        let countsArr = Object.keys(counts).map((vers, idx) => {
            let color;
            if (idx >= colorMap.length) {
                // random color
                color = "#" + Math.random().toString(16).slice(-6);
            } else {
                color = colorMap[idx];   
            }
            return {
                name: `(${vers})`,
                count: counts[vers],
                color: color,
                legendFontColor: '#7F7F7F',
                legendFontSize: 15,
            };
        })

        setversionCounts(countsArr);
    };

    useEffect(() => {
        // subscribe to our data provider
        DataProv.subscribe(handleNewData);
    }, [])

    useEffect(() => {
        // subscribe to our data provider
        handleNewData(Device.getDevices());
    }, [props.connectedOnly])

    return (
        <View style={[styles.chartContainer]}>
            <View style={[styles.chartWrap]}>
                <Text style={styles.chartHeader}>{chartTitle}</Text>
                <PieChart
                    accessor="count"
                    data={versionCounts}
                    width={getChartWidth()}
                    height={150}
                    chartConfig={chartConfig}
                    backgroundColor={"#FFFFFF00"}
                    withCustomBarColorFromData
                    flatColor
                    absolute
                />
            </View>
        </View>
    );
}

function WifiSignal(props) {
    const [wifiSignals, setWifiSignals] = useState(EMPTY_BAR_DATA);

    const handleNewData = (devices) => {
        let signalGroups = {"?": 0};

        for (let d in devices) {
            if (props.connectedOnly && devices[d].status == 99) continue;

            let thisSignal = devices[d].system.wifiRxSignaldB;
            thisSignal = parseInt(thisSignal);

            if (isNaN(thisSignal)) {
                signalGroups["?"] += 1;
            } else {
                // round to nearest 10
                thisSignal = Math.ceil(thisSignal / 10) * 10;
                
                if (signalGroups[thisSignal] == null) {
                    signalGroups[thisSignal] = 1;
                } else {
                    signalGroups[thisSignal] += 1;
                }
            }
        }

        let dataObj = {...EMPTY_BAR_DATA}; 
        dataObj.labels = Object.keys(signalGroups).sort();
        dataObj.datasets = [{
                data: dataObj.labels.map(l => signalGroups[l]),
                colors: dataObj.labels.map((l, idx) => () => `rgb(${idx*40}, ${255 - idx*40}, 0)`)
            }
        ];
      
        setWifiSignals(dataObj);
    };

    useEffect(() => {
        // subscribe to our data provider
        DataProv.subscribe(handleNewData);
        // DEV: 
        handleNewData(TEST_DEVICES);
    }, [])

    useEffect(() => {
        // subscribe to our data provider
        handleNewData(Device.getDevices());
    }, [props.connectedOnly])

    return (
        <CommonBarChart 
            key={"wifi"}
            data={wifiSignals}
            title={"Wifi Signal Level"}
            verticalLabelRotation={screenWidth > 400 ? undefined : 30}
            yLabelsOffset={10}
            width={screenWidth > 400 ? screenWidth * 0.7 : screenWidth-20}
            chartConfig={{
                ...chartConfig,
                barPercentage: screenWidth > 400 ? 0.8 : 0.3
            }}
            style={{
                marginLeft: -40, 
                width: screenWidth > 400 ? screenWidth * 0.7 : screenWidth-20
            }}
        />
    );
}


function SideBySideOrStacked(props) {
    if (screenWidth > 400) {
        return (
            <View style={{flexDirection: "row"}}>
                {props.children}
            </View>
        )
    }
    else {
        return props.children;
    }
}


function Dashboard(props) {
    return (
        <View style={{flex: 1, margin: 8, alignItems: "center"}}>
            <ConnectedDisconnected/>
            <SideBySideOrStacked>
                <VersionPieChart processor="ESP" connectedOnly={props.connectedOnly}/>
                <VersionPieChart processor="MSP" connectedOnly={props.connectedOnly}/>
            </SideBySideOrStacked>
            <WifiSignal connectedOnly={props.connectedOnly}/>
        </View>
    );
}

function ScreenHeader(props) {

    return (
        <View style={styles.screenHeaderWrap}>
            <View style={styles.screenHeader}>
                <Text style={styles.screenHeaderTxt}>AWACS Prototype Admin</Text>
                <Pressable style={[styles.button, {backgroundColor: props.connectedOnly ? "#99FFAA" : "#CFCFCF"}]} 
                onPress={() => {
                    props.setConnectedFilter(!props.connectedOnly);
                }}>
                    <Ionicons name="md-filter" size={22} color="green" />
                </Pressable>
            </View>
        </View>
    )
}

export default function App() {
  const [connectedOnly, setConnectedOnly] = useState(false);

  return (
    <SafeAreaView style={styles.root}>
        <View style={styles.container}>
            <StatusBar style="auto" />
            <ScreenHeader connectedOnly={connectedOnly} setConnectedFilter={(isFiltered) => 
                setConnectedOnly(isFiltered)
            }/>
            <ScrollView>
                <Dashboard connectedOnly={connectedOnly} />
            </ScrollView>
        </View>
    </SafeAreaView>
  );
}

