import * as CONST from "./constants";

let devices = {};

export default class Device {

    constructor(id) {
        this.id = id;
        this.status = null;
        this.events = [];
        this.system = {};
        this.metadata = {};
        this.params = {};
    }

    updateSystemProp(sysProp, msg) {
        if (CONST.MQTT_MAP["system"][sysProp] == null) {
            // invalid system property. ignore
            // console.log("invalid system property: ", sysProp);
            return;
        }

        this.system[sysProp] = msg;
    }

    updateMetadataProp(metaProp, msg) {
        if (CONST.MQTT_MAP["metadata"][metaProp] == null) {
            // invalid system property. ignore
            // console.log("invalid metadata property: ", metaProp);
            return;
        }

        this.metadata[metaProp] = msg;
    }

    updateParamProp(paramProp, msg) {
        if (CONST.MQTT_MAP["params"][paramProp] == null) {
            // invalid system property. ignore
            // console.log("invalid params property: ", paramProp);
            return;
        }

        this.params[paramProp] = msg;
    }

    updateEventProp(eventTypePathArr, msg) {
        let eventType = eventTypePathArr.join(".");

        this.events.push({type: eventType, data: msg});

        let data = null;

        // parse message as json
        try {
            data = JSON.parse(msg);

            if (data == null) {
                console.log("Failed to parse event msg as json: ", msg, e.message);
                return;
            }
        } catch (e) {
            console.log("Failed to parse event msg as json: ", msg, e.message);
            return;
        }

        if (eventType == "status") {
            if (data["status"] != null) {
                this.status = data.status;
            } else if (data["esp version"] != null) {
                this.system.espFwRevision = data["esp version"];
            } else if (data["TI version"] != null) {
                this.system.TiFwRevision = data["TI version"];
            }
        }
    }

    updateFromRx(topic, msg) {
        let elms = topic.split("/");
        
        let subtopic = elms[3];

        if (subtopic == "system") {
            this.updateSystemProp(elms[4], msg);
        } else if (subtopic == "metadata") {
            this.updateMetadataProp(elms[4], msg);
        } else if (subtopic == "params") {
            this.updateParamProp(elms[4], msg);
        } else if (subtopic == "events") {
            this.updateEventProp(elms.slice(4), msg);
        }
    }

    toString() {
        let s = ["----------------------------------------"];
        s.push(this.id);
        s.push("----------------------------------------");
        s.push("Status: " + this.status);

        s.push("----System Properties----");
        for (let t in this.system) {
            s.push(t + ": " + this.system[t]);
        }

        s.push("----Metadata----");
        for (let t in this.metadata) {
            s.push(t + ": " + this.metadata[t]);
        }

        s.push("----Sensor Parameters----");
        for (let t in this.params) {
            s.push(t + ": " + this.params[t]);
        }

        return s.join("\n");
    }

    static extractDeviceString(topic) {
        let elms = topic.split("/");

        if (elms.length <= 3) {
            return null;
        }

        let devStr = elms[2];

        if (!devStr.startsWith("AW") || devStr.length != 6)
        {
            return null;
        }

        return devStr;
    }

    static getDeviceForTopic(topic) {
        let devIdStr = Device.extractDeviceString(topic);

        if (!devIdStr) {
            return null;
        }

        if (devices[devIdStr] != null) {
            // return existing device for this device string
            return devices[devIdStr];
        }

        // device not found yet. Create it and add it to our data store.
        devices[devIdStr] = new Device(devIdStr);

        return devices[devIdStr];
    }

    static getDevices() {
        return devices;
    }
}
